import { usersAPI } from "../API/api";
import { updateObjectInArray } from "../utils/helpers/objectsHelpers";

const FOLLOW = 'FOLLOW';
const UNFOLLOW = 'UNFOLLOW';
const SET_USERS = 'SET_USERS';
const SET_CURRENT_PAGE = 'SET_CURRENT_PAGE';
const SET_TOTAL_USERS_COUNT = 'SET_TOTAL_USERS_COUNT';
const TOOGLE_IS_FETCHING = 'TOOGLE_IS_FETCHING';
const TOOGLE_IS_FOLLOWING_PROGRESS = 'TOOGLE_IS_FOLLOWING_PROGRESS';


let initialState = {
    users:[],
    pageSize: 10,
    totalUsersCount: 0,
    currentPage: 1,
    isFetching: true,
    followingInProgress: [],
  };
const usersReducer = ( state = initialState, action) => {
    switch (action.type) {
        case FOLLOW:
            return {
                ...state, 
                users: updateObjectInArray(state.users, action.userId, 'id', {followed : true})
            };
        case UNFOLLOW:
            return {
                ...state, 
                users: updateObjectInArray(state.users, action.userId, 'id', {followed:false})
            };
        case SET_USERS:
          return { ...state, users: action.users};
        case SET_CURRENT_PAGE:
            return { ...state, currentPage: action.currentPage};
        case SET_TOTAL_USERS_COUNT:
            return { ...state, totalUsersCount: action.totalCount};
        case TOOGLE_IS_FETCHING:
            return { ...state, isFetching: action.isFetching};
        case TOOGLE_IS_FOLLOWING_PROGRESS:
            return { ...state, 
              followingInProgress: action.isFetching 
              ?  [...state.followingInProgress, action.userId]
              : [...state.followingInProgress.filter(id => id != action.userId)] 
            }
        default:
            return state;
    }
}

export const followSuccess = (userId) => {
    return {
      type: FOLLOW,
      userId
    }
  }
export const unfollowSuccess = (userId) => {
    return {
      type: UNFOLLOW,
      userId
    }
  }
export const setUsers = (users) => {
    return {
      type: SET_USERS,
      users
    }
  }
export const setCurrentPage = (currentPage) => {
    return {
      type: SET_CURRENT_PAGE,
      currentPage
    }
  }
export const setTotalUsersCounts = (totalCount) => {
    return {
      type: SET_TOTAL_USERS_COUNT,
      totalCount
    }
  }
export const setIsFetching = (isFetching) => {
    return {
      type: TOOGLE_IS_FETCHING,
      isFetching
    }
  }
export const toogleIsFollowingInProgress = (isFetching, userId) => {
    return {
      type: TOOGLE_IS_FOLLOWING_PROGRESS,
      isFetching,
      userId
    }
  }
export const getUsersThunkCreator = (page, pageSize) => {
    return async (dispatch) => {
        dispatch(setIsFetching(true));
        dispatch(setCurrentPage(page))
  
        let data = await usersAPI.getUsers(page, pageSize)
        dispatch(setIsFetching(false));
        dispatch(setUsers(data.items));
        dispatch(setTotalUsersCounts(data.totalCount));
        };  
      } 

const followUnfollowFlow = async (dispatch, userId, apiMethod, actionCreator) => {
    dispatch(toogleIsFollowingInProgress(true, userId))
    let response = await apiMethod(userId)
        
    if (response.data.resultCode === 0) {
      dispatch(actionCreator(userId))
    }
    dispatch(toogleIsFollowingInProgress(false, userId))
  }
  
export const follow = (userId) => {
    return async (dispatch) => {
      followUnfollowFlow(dispatch, userId, usersAPI.follow.bind(usersAPI), followSuccess)
    }
  } 
  
export const unfollow = (userId) => { 
  return async (dispatch) => {
    followUnfollowFlow(dispatch, userId, usersAPI.unfollow.bind(usersAPI), unfollowSuccess)
  } 
}
export default usersReducer;
