

const ADD_MESSAGE = 'ADD-MESSAGE';

let initialState = {
    messagesData: [
        {id: 1, message: "Hsadasi"}, 
        {id: 2, message: "Andrey"},
        {id: 3, message: "Sveta"},
        {id: 4, message: "Valera"},
        {id: 5, message: "Viktor"},
        {id: 6, message: "Sasha"}
    ],
    dialogsData: [
        {id: 1, name: "Dima"},
        {id: 2, name: "Andrey"},
        {id: 3, name: "Sveta"},
        {id: 4, name: "Valera"},
        {id: 5, name: "Viktor"},
        {id: 6, name: "Sasha"},
    ],
   
}; 

const messageReducer = (state = initialState,action) => {
    switch (action.type) {
        case ADD_MESSAGE:
            let text = action.newMessageText;
            return {
                ...state,
                dialogsData: [...state.dialogsData, {id: 7, name: text}],
            };
        default:
            return state;
    }
}

export const addMesssageCreator = (newMessageText) => {
return {
    type: ADD_MESSAGE,
    newMessageText
    }
}
export default messageReducer;