import React from 'react';
import classes from './friends.module.css';

const Friend = (props) => {
    return (
        <div >{props.name} name</div>
        );
}


const Friends = (props) => {
    
    let friendsElements = props.friendsData.map(friend => <Friend name={friend.name}/>);
    return (
        <div >
            <div >
                {
                    friendsElements
                }
            </div>
        </div>
    );
}

export default Friends;