import React, { Component, Suspense } from 'react';
import Nav from '../nav/nav';
import './App.css';
import { Route, withRouter } from 'react-router-dom';
import News from '../profile/news/news';
import Music from '../profile/Music/music';
import Settings from '../profile/settings/setiings';
import Friends from '../profile/friends/friends';
import UsersContainer from '../Users/usersContainer';
import HeaderContainer from '../app-header/headerContainer';
import Login from '../login/login';
import { connect } from 'react-redux';
import { compose } from 'redux';
import { initializeApp } from '../../redux/appReducer';
import Preloader from '../common/preloader';
import { withSuspense } from '../../hoc/withSuspense';


const DialogsContainer = React.lazy(() => import('../profile/Dialogs/dialogsContainer'));
const ProfileContainer = React.lazy(() => import('../profile/profileContainer'));

class App extends Component {
  
  componentDidMount() {
    this.props.initializeApp();
  }
  
  render() {
    if (!this.props.initialized) {
      return <Preloader/>
    }
    return (
        <div className='app-wrapper'>
          <HeaderContainer/>
          <Nav/>
          <div className='app-wrapper-content'>
            <Route path='/dialogs' render={withSuspense(DialogsContainer)} />

            <Route path='/profile/:userId?' render={withSuspense(ProfileContainer)} />

            <Route path='/users' render={ () => <UsersContainer/>}/>

            <Route path='/login' render={ () => <Login/>}/>

            <Route path='/news' component={News}/>

            <Route path='/music' component={Music}/>

            <Route path='/settings' component={Settings}/>

            <Route path='/friends' render={ () => <Friends 
              friendsData={this.props.state.sidebar.friendsData} />} />
          </div>
        </div>
      );
    }
  }
const mapStateToProps = (state) => ({
  initialized: state.app.initialized
})

export default compose(
  withRouter,
  connect(mapStateToProps, {initializeApp})) (App);
  